.modal-box {
  background-color: black;
  color: white;
  width: 200px;
  height: 200px;
  border-radius: 6px;
  margin: 0 auto;
  text-align: center;
}
.modal-box > p {
  padding-top: 80px;
}

.modal-wrapper {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
